/*
    Stop mouseOver event firing when pointer is over text.
    Without this, onMouseOver will fire when mouse is over
    marker and when it moves over the marker text.
*/
text {
    pointer-events: none;
}

.zoom {
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    -o-transition: transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s; /* Animation */
}

.zoom:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}



